
.spanClassName {
    color: #044c04;
    font-size: 30px;
}
.checkbox {
    --background: #a4f065;
    --border: #d1d6ee;
    --border-hover: #bbc1e1;
    --border-active: #1e5c18;
    --tick: #fff;
    position: relative;
}
.checkbox input,
.checkbox svg {
    width: 21px;
    height: 21px;
}
.checkbox input:hover {
    --s: 2px;
    --b: var(--border-hover);
}
.checkbox input {
    -webkit-appearance: none;
    -moz-appearance: none;
    outline: none;
    background: var(--background);
    border: none;
    margin: 0;
    padding: 0;
    cursor: pointer;
    border-radius: 4px;
    transition: box-shadow 0.3s;
    box-shadow: inset 0 0 0 var(--s) var(--b, var(--border));
}

.checkbox input:checked {
    --b: var(--border-active);
}
.checkbox svg {
    pointer-events: none;
    fill: none;
    stroke-width: 2px;
    stroke-linecap: round;
    stroke-linejoin: round;
    stroke:  var(--border-active);
    position: absolute;
    top: 0;
    left: 0;
    width: 21px;
    height: 21px;
    transform: scale(1) translateZ(0);
}
.checkbox.path input:checked {
    --s: 2px;
    transition-delay: 0.4s;
}
.checkbox.path input:checked + svg {
    --a: 16.1 86.12;
    --o: 102.22;
}
.checkbox.path svg {
    stroke-dasharray: var(--a, 86.12);
    stroke-dashoffset: var(--o, 86.12);
    transition: stroke-dasharray 0.6s, stroke-dashoffset 0.6s;
}


.spanClassName{
    padding-left: 5px;
}
label{
    display: flex;
    justify-content: center;
}




